
/* ====================== Breadcrumb Css Start ==================== */ 
.breadcrumb {
    margin-bottom: 0px;
    &__list {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 8px;
    }
    &__item{
        color: hsl(var(--black));  
        font-weight: 400;
        @extend .fs-16; 
        &-text {
            font-weight: 700;
        }
    }
    &__link {
        color: hsl(var(--black));  
        font-weight: 500;
        &:hover {
            color: hsl(var(--base)); ;
        }
    }
}
/* ====================== Breadcrumb Css End ==================== */
