.service-step-thumbs {
    background-color: hsl(var(--bg-color));
    border-radius: 8px;
    padding: 12px;

    img {
        width: 100%;
        border-radius: inherit;
    }
}

.service-step-content {
    padding-left: 28px;

    @include md-screen {
        padding-left: 0px;
    }
}

.service-step-item {
    counter-increment: counter;
    gap: 24px;
    position: relative;

    &__content {
        flex: 1;
    }

    &:not(:last-child)::after {
        position: absolute;
        content: "";
        height: 100%;
        width: 4px;
        background-color: hsl(var(--border-color));
        top: 0px;
        left: 27px;
        z-index: -1;

        @include msm-screen {
            left: 22px;
        }
    }

    &::before {
        content: counter(counter);
        height: 56px;
        width: 56px;
        flex-shrink: 0;
        background: var(--background-gradient);
        border-radius: 50%;
        display: grid;
        place-content: center;
        color: hsl(var(--white));
        font-size: 20px;
        font-weight: 500;
        outline: 6px solid hsl(var(--white));

        @include msm-screen {
            height: 46px;
            width: 46px;
        }
    }

    &:not(:last-child) {
        padding-bottom: 75px;

        @include xxl-screen {
            padding-bottom: 60px;
        }

        @include lg-screen {
            padding-bottom: 40px;
        }

        @include md-screen {
            padding-bottom: 70px;
        }

        @include sm-screen {
            padding-bottom: 50px;
        }

        @include msm-screen {
            padding-bottom: 40px;
        }
    }

    &__title {
        margin-bottom: 16px;
        font-weight: 600;

        @include lg-screen {
            margin-bottom: 10px;
        }
    }
}
