.service-banner {
    .breadcrumb {
        &__item {
            i {
                color: hsl(var(--base));
            }
        }
    }

    padding-top: clampCal(24, 40);
    padding-bottom: clampCal(40, 90);
    position: relative;
    z-index: 1;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(90deg, hsl(var(--black)) 26%, hsl(var(--black) / 0.06) 100%);
        height: 100%;
        width: 100%;
        z-index: -1;
    }

    @include md-screen {
        margin-top: 60px;
    }

    .breadcrumb {
        margin-bottom: clampCal(30, 64);

        .breadcrumb__item {
            color: hsl(var(--white));
        }

        &__link {
            color: hsl(var(--white));
        }
    }

    &__content {
        max-width: 540px;
        width: 100%;
    }

    &__title {
        color: hsl(var(--white));
        font-weight: 500;
        margin-bottom: 12px;
        line-height: 1.2;
    }

    &__text {
        gap: 6px;
        color: hsl(var(--white) / .9);

        .dot {
            color: hsl(var(--base));
        }
    }

    .service-banner-list {
        margin-top: clampCal(24, 40);
        display: flex;
        flex-direction: column;
        gap: 16px;

        &__item {
            display: flex;
            align-items: center;
            gap: 16px;
        }

        &__iamge {
            flex-shrink: 0;

            img {
                height: 32px;
                width: 32px;
                display: block;
            }
        }

        &__text {
            color: hsl(var(--white) / .9);
            @extend .fs-20;
        }
    }
}

.service-details {
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: 40px;

        @include md-screen {
            flex-direction: column;
        }
    }

}

.service-overview {
    max-width: 600px;

    @include md-screen {
        max-width: 100%;
    }

    &__steptitle {
        font-weight: 600;
        margin-bottom: clampCal(10, 16);
    }

    &__title {
        padding: 16px;
        border-radius: 8px;
        background-color: hsl(var(--bg-color));
        color: hsl(var(--base));
        font-weight: 600;
        margin-bottom: clampCal(16, 24);
    }

    &__col {
        margin-bottom: clampCal(16, 24);
    }

    &__subheading {
        font-weight: 600;
        margin-bottom: 0;
    }

    &__heading {
        font-size: 16px;
        font-weight: 600;
        margin-bottom: clampCal(10, 16);
    }

    &__list {
        padding-left: 20px;

        .item {
            list-style: disc;
            color: hsl(var(--black));

            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }

    &__step {
        .item {
            position: relative;
            display: flex;
            counter-increment: serviceStep;

            &:not(:last-child) {
                padding-bottom: 16px;
            }

            gap: 16px;

            &::before {
                content: counter(serviceStep);
                height: 24px;
                width: 24px;
                flex-shrink: 0;
                background: var(--background-gradient);
                border-radius: 50%;
                display: grid;
                place-content: center;
                color: hsl(var(--white));
                font-size: 16px;
                font-weight: 600;
            }

            &:not(:last-child)::after {
                position: absolute;
                content: "";
                height: 100%;
                width: 2px;
                background-color: hsl(var(--border-color));
                top: 0px;
                left: 12px;
                z-index: -1;
            }

            .content {
                .heading {
                    font-size: 16px;
                    font-weight: 600;
                    margin-bottom: 10px;
                }
            }
        }
    }
}

.service-box {
    max-width: 620px;
    width: 100%;
    padding: clampCal(16, 24);
    border-radius: 8px;
    background: linear-gradient(90deg, hsl(var(--base-l-300)) 0%, hsl(var(--base)) 100%);
    position: sticky;
    top: 20%;
    margin-top: -180px;
    z-index: 2;

    @include md-screen {
        max-width: 100%;
        position: inherit;
        margin-top: 0;
    }

    &__title {
        font-weight: 600;
        color: hsl(var(--white));
        margin-bottom: clampCal(16, 24);
    }

    .item {
        background-color: hsl(var(--white));
        padding: clampCal(10, 16);
        border-radius: 8px;
        display: flex;
        align-items: center;
        gap: clampCal(20, 40);
        flex-wrap: wrap;
        cursor: pointer;

        &:not(:last-child) {
            margin-bottom: 16px;
        }

        &__heading {
            flex: 1;
            margin-bottom: 0px;
        }

        &__button {
            flex-shrink: 0;

            .btn {
                padding: 10px 24px;
                font-size: 24px;
            }
        }
    }
}

.service-modal {
    .modal-dialog {
        max-width: 950px;
        width: 100%;
    }

    .modal-content {
        border: 0;
        box-shadow: var(--box-shadow);
        border-radius: 8px;
    }

    .modal--header {
        box-shadow: var(--sm-box-shadow);
        position: relative;
        padding: 16px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .modal-title {
            font-weight: 600;
        }

        .btn-close {
            background-image: none;
            background-color: hsl(var(--bg-color));
            opacity: 1;
            color: hsl(var(--base));
            height: 40px;
            width: 40px;
            border-radius: 50%;
            display: grid;
            place-content: center; 
            font-size: 24px;
            transition: all linear 0.3s;

            &:hover{
                background-color: hsl(var(--base));
                color: hsl(var(--white));
            }
        }
    }
}

.service-package-content {
    max-height: 500px;
    min-height: 500px;
    overflow-y: auto;
    border-right: 2px solid hsl(var(--border-color));

    @include md-screen {
        border-bottom: 2px solid hsl(var(--border-color));
    }

    @include sm-screen {
        border-right: none;
        min-height: auto;
        max-height: unset;
    }


    &::-webkit-scrollbar {
        width: 4px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        background-color: hsl(var(--bg-color));
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: hsl(var(--border-color));
        border-radius: 4px;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: hsl(var(--base));
    }

}

.service-package-item {
    &:not(:last-child) {
        margin-bottom: 16px;
        border-bottom: 1px solid hsl(var(--border-color));
    }

    &:first-child {
        margin-top: 16px;
    }

    padding: 0px 16px 16px;

    .wrapper {
        display: flex;
        align-items: flex-start;
        gap: 16px;
        justify-content: space-between;

        .item-left {
            display: flex;
            align-items: flex-start;
            gap: 16px;

            @include xxsm-screen {
                flex-wrap: wrap;
            }
        }

        @include md-screen {
            flex-wrap: wrap;
        }

        .image {
            border-radius: 8px;
            overflow: hidden;
            max-width: 75px;
            height: 65px;

            img {
                height: 100%;
                object-fit: cover;
                object-position: left center;
            }
        }

        .content {
            .title {
                font-weight: 500;
                line-height: 1.5;
                margin-bottom: 8px;
            }

            .list {
                padding-left: 20px;

                &__item {
                    list-style: disc;
                    font-size: 14px;
                }
            }
        }

        .item-right {
            flex-shrink: 0;
            text-align: right;

            @include md-screen {
                text-align: left;
            }

            .btn {
                padding: 8px 16px;
                @extend .fs-14;
                line-height: 1.5;
                display: inline-flex;
                align-items: center;
                gap: 8px;
                border-width: 1px !important;

                span {
                    font-size: 24px;
                    display: flex;
                }
            }
        }
    }
}

.package-item-price {
    font-size: 16px;
    color: hsl(var(--black));
    line-height: 1.5;
    margin-top: 8px;

    .price {
        color: hsl(var(--success));
        display: inline-block;
        font-weight: 700;
    }
}

.service-package-header {
    padding: 16px;
    background-color: hsl(var(--bg-color));
    border-radius: 8px;
    margin-bottom: 16px;

    .text {
        color: hsl(var(--black));

        .icon {
            color: hsl(var(--base));
            font-size: 24px;
        }
    }
}

.package-cart {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    max-height: 500px;
}

.service-package-cart {
    padding: 16px 12px;
    flex: 1;
    overflow-y: auto;

    &::-webkit-scrollbar {
        width: 8px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
        background-color: hsl(var(--bg-color));
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: hsl(var(--border-color));
        border-radius: 4px;
    }

    /* Handle on hover */
    &::-webkit-scrollbar-thumb:hover {
        background: hsl(var(--base));
    }

    .item {
        &:not(:last-child) {
            padding-bottom: 16px;
            margin-bottom: 16px;
            border-bottom: 1px solid hsl(var(--border-color));
        }

        &__wrapper {
            display: flex;
            align-items: flex-start;
            flex-wrap: wrap;

            .title {
                width: 50%;
                color: hsl(var(--black));
                font-weight: 500;

                @include md-screen {
                    width: 100%;
                    margin-bottom: 10px;
                }
            }
        }

        &__right {
            width: 50%;

            @include md-screen {
                width: 100%;
            }
        }

        &__text {
            font-size: 14px;
            max-width: 210px;
            width: 100%;
            margin-top: 8px;
        }
    }
}

.package-cart-price {
    text-align: right;
    font-weight: 600;
    margin-top: 8px;
    line-height: 1.5;
    @extend .fs-14;
    color: hsl(var(--success));

    @include md-screen {
        text-align: left;
    }
}

.package-checkout {
    flex-shrink: 0;
    padding: 12px 24px;
    @extend .fs-18;
    border-radius: 0;
}

.quantity {
    display: flex;
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;
    align-items: stretch;
    width: 176px;

    &__btn {
        color: hsl(var(--base)) !important;
        padding: 4px 8px;
        font-size: 24px;
        flex-shrink: 0;
    }

    &__input {
        width: 40px;
        outline: none;
        border: none;
        border-left: 1px solid hsl(var(--border-color));
        padding-left: 12px;
    }

    &__text {
        border-right: 1px solid hsl(var(--border-color));
        flex-shrink: 0;
        vertical-align: middle;
        display: grid;
        place-content: center;
        padding-right: 12px;
    }
}
