/* ================================= Table Css Start =========================== */
/* Table Content Css start */
.table--collapse {
	margin: 0;
	border-collapse: separate !important;
	border-spacing: 0 8px !important;

	thead {
		tr {
			th {
				text-align: center !important;
				&:first-child {
					border-radius: 8px 0px 0px 8px !important;
				}

				&:last-child {
					border-radius: 0 6px 8px 0 !important;
				}
			}
		}
	}

	tbody {
		border: 0 !important;
		background-color: hsl(var(--white));

		tr {
			&:last-child {
				border-bottom: 0;
			}

			td {
				border-top: 1px solid hsl(var(--border-color)) !important;
				border-bottom: 1px solid hsl(var(--border-color)) !important;
				border-width: 1px;
				padding: 24px 16px !important;
				border: 0;
				max-width: 340px;
				text-align: center !important;
				
				&:first-child {
					text-align: left;
					border-left: 1px solid hsl(var(--black) / .08);
					@extend .fs-14;
					border-radius: 8px 0 0 8px;
				}

				&:last-child {
					text-align: right;
					border-right: 1px solid hsl(var(--black) / .08);
					border-radius: 0px 8px 8px 0;
				}
			}
		}
	}
}

.details-btn {
	color: hsl(var(--base));
	.icon{
		margin-left: 5px;
		font-size: 20px;
	}
}

.table-top {
	margin-bottom: clampCal(16, 24);

	&__wrapper {
		display: flex;
		align-items: center;
		gap: clampCal(16, 24);
	}

	.table-search {
		max-width: 200px;
		width: 100%;
		color: hsl(var(--light));
		position: relative;

		&__icon {
			position: absolute;
			height: 100%;
			width: 48px;
			display: grid;
			place-content: center;
			transform: scale(1.5) rotate(-90deg);
			color: hsl(var(--black));
		}

		input {
			padding-left: 48px;
		}

		select {
			line-height: 20px;
		}

		.form--control {
			color: hsl(var(--light));

			&::placeholder {
				font-size: 16px;
				color: hsl(var(--black) / .5);
			}
		}
	}
}

.customer {
	display: flex;
	align-items: center;
	flex-wrap: wrap;

	@include xxsm-screen {
		display: block;
		text-align: left;
	}

	&__thumb {
		width: 64px;
		height: 64px;
		border-radius: 8px;
		overflow: hidden;
		flex-shrink: 0;

		@include xxsm-screen {
			margin-left: auto;
		}
	}

	&__content {
		padding-left: 15px;
		text-align: left;
		flex: 1;

		@include xsm-screen {
			padding-left: 8px;
		}

		@include xxsm-screen {
			width: 100%;
			padding-left: 0px;
			padding-top: 5px;
		}
	}

	&__name {
		margin-bottom: 0;
		@extend .fs-16;
		color: hsl(var(--black));
		font-weight: 500;
	}
}

.action-buttons {
	@extend .flex-align;
	gap: 10px;
	justify-content: flex-end;
}

.action-btn {
	width: 35px;
	height: 35px;
	border-radius: 50%;
}

.edit-btn {
	color: hsl(var(--info));
	background-color: hsl(var(--info) / .08)
}

.delete-btn {
	color: hsl(var(--danger));
	background-color: hsl(var(--danger) / .08);
}

/* Table Content Css end */

/* Table Css Start */
.table {
	margin: 0;
	border-collapse: collapse;
	border-collapse: separate;
	border-spacing: 0px 0px;

	thead {
		tr {
			th {
				background-color: hsl(var(--bg-color));
				text-align: left;
				padding: 16px;
				color: hsl(var(--black));
				font-family: var(--heading-font);
				font-weight: 600;
				border-bottom: 0;
				white-space: nowrap;
				@extend .fs-16;

				&:first-child {
					border-radius: 8px 0px 0px 0px;
				}

				&:last-child {
					border-radius: 0 8px 0px 0;
					text-align: right;
				}

				&:nth-child(2) {
					text-align: left;
				}
			}
		}
	}

	tbody {
		border: 0 !important;
		background-color: hsl(var(--white));

		tr {
			&:last-child {
				border-bottom: 0;
			}

			&:last-child {
				td {
					&:first-child {
						border-radius: 8px 0 0 8px;
					}

					&:last-child {
						border-radius: 0px 8px 8px 0;
					}
				}
			}

			td {
				text-align: left;
				vertical-align: middle;
				padding: 16px;
				border-width: 1px;
				border: 0;
				font-family: var(--heading-font);
				color: hsl(var(--black));
				font-weight: 500;
				max-width: 340px;
				@extend .fs-16;

				&::before {
					content: attr(data-label);
					font-family: var(--heading-font);
					@extend .fs-15;
					color: hsl(var(--black));
					font-weight: 500;
					display: none;
					width: 45% !important;
					text-align: left;
				}

				&:first-child {
					text-align: left;
					@extend .fs-14;
				}

				&:last-child {
					text-align: right;
				}
			}
		}
	}
}

.table--responsive--md {
	@include sm-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				&:last-child td {
					border-bottom: 0;
				}

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--black)/.8);
					max-width: unset;

					&:last-child {
						border: none;
					}

					&:first-child {
						text-align: right;
						border-left: 0;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	@include sm-screen {
		tbody {
			tr {
				td {
					border: 0;
				}
			}
		}
	}
}

.table--responsive--lg {
	@include md-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				&:nth-child(even) {
					background-color: hsl(var(--black) / .02);
				}

				&:last-child td {
					border-bottom: 0;
				}

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--border-color));
					max-width: unset;

					&:last-child {
						border: none;
					}

					&:first-child {
						text-align: right;
						border-left: 0;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	@include md-screen {
		tbody {
			tr {
				td {
					border: none;
				}
			}
		}
	}
}

.table--responsive--xl {
	@include lg-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;
				box-shadow: none;

				&:nth-child(odd) {
					background-color: hsl(var(--bg-color));
				}

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 16px 24px;
					border: none;
					max-width: unset;
					border: none !important;

					&:last-child {
						border-radius: 0px 0px 8px 8px !important;
					}

					&:first-child {
						text-align: right;
						border-left: 0;
						border-radius: 8px 8px 0px 0px !important;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	@include lg-screen {
		tbody {
			tr {
				td {
					border: 0;
				}
			}
		}
	}
}

.table--responsive--xxl {
	@include xl-screen {
		thead {
			display: none;
		}

		tbody {
			tr {
				display: block;

				&:last-child td {
					border-bottom: 0;
				}

				td {
					display: flex;
					align-items: center;
					justify-content: space-between;
					gap: 15px;
					text-align: right;
					padding: 10px 15px;
					border: none;
					border-bottom: 1px solid hsl(var(--border-color));
					max-width: unset;

					&:last-child {
						border: none;
					}

					&:first-child {
						text-align: right;
						border-left: 0;
					}

					&::before {
						display: block;
					}
				}
			}
		}
	}

	@include xl-screen {
		tbody {
			tr {
				td {
					border: 0;
				}
			}
		}
	}
}

.search{
	max-width: 230px;
	width: 100%;
	position: relative;

	.form--control{
		padding-right: 48px !important;
	}

	&__icon{
		position: absolute;
		top: 0px;
		right: 0px;
		font-size: 24px;
		height: 100%;
		width: 48px;
		display: grid;
		place-content: center;
		cursor: pointer;
		color: hsl(var(--base));
	}
}



/* ================================= Table Css End =========================== */
