/* ================================= Common Typography Css Start =========================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--body-font);
  color: hsl(var(--body-color));
  word-break: break-word;
  background-color: hsl(var(--white));
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

p {
  @extend .fs-16;
  font-weight: 400;
  margin: 0;
}

span {
  display: inline-block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 20px 0;
  font-family: var(--heading-font);
  color: hsl(var(--heading-color));
  line-height: 1.3;
  font-weight: 700;

  @include sm-screen {
    margin: 0 0 15px 0;
  }
}

h1 {
  font-size: clampCal(28, 64); // Min:40px - Max:64px
}

h2 {
  font-size: clampCal(24, 40); // Min: 24px - Max: 40px
}

h3 {
  font-size: clampCal(20, 32); // Min: 20px - Max: 32px
}

h4 {
  font-size: clampCal(18, 24); // Min: 18px - Max: 24px
}

h5 {
  font-size: clampCal(16, 20); // Min: 16px - Max: 20px
}

h6 {
  font-size: clampCal(16, 18); // Min: 16px - Max: 18px
}

h1>a,
h2>a,
h3>a,
h4>a,
h5>a,
h6>a {
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  transition: .2s linear;
  line-height: 1.3;
}

a {
  display: inline-block;
  transition: .2s linear;
  text-decoration: none;
  color: #0667f6;
}

a:hover {
  color: #2f83ff;
}

img {
  max-width: 100%;
  height: auto;
}

select {
  cursor: pointer;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

button {
  border: 0;
  background-color: transparent;

  &:focus {
    outline: none;
    box-shadow: none;
  }
}

.form-select:focus {
  outline: 0;
  box-shadow: none;
}

/* ================================= Common Typography Css End =========================== */
