
.category-item {
    background-color: hsl(var(--bg-color));
    display: block;
    text-align: center;
    padding: 32px 12px;
    border-radius: 8px;
    height: 100%;
    display: grid;
    place-content: center;
    transition: all linear 0.3s;

    &:hover {
        box-shadow: var(--sm-box-shadow);
        .category-item{
            &__heading {
                color: hsl(var(--base));
            }
        }
    }

    &__icon {
        font-size: 64px;
        margin: 0px auto 24px;
        display: flex;
        color: hsl(var(--base));
    }

    &__viewicon {
        font-size: 32px;
    }

    &__heading {
        margin-bottom: 8px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        font-weight: 500;
        transition: all linear 0.3s;

        @include md-screen{
            font-weight: 600;
        }
    }

    &__count {
        color: hsl(var(--body-color));
        .number{
            margin-right: 5px;
        }
    }

}

