.table--scroller{
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;
    overflow: hidden;
    position: relative;
    height: 240px;
}

.table--load {
    position: absolute;
    bottom: 0px;
    right: 0;
    width: 100%;
    padding: 16px;
    z-index: 5;
    background: linear-gradient(90deg, transparent 0.03%, hsl(var(--white)) 78.16%);
    cursor: pointer;
    font-weight: 600;
    color: hsl(var(--base));
    display: flex;
    gap: 8px;
    justify-content: flex-end;

    .expanded &{
        background: transparent;
        position: inherit;
    }
}

.walet-card{
    padding: 16px 16px 30px;
    border-radius: 8px;
    background-color: hsl(var(--bg-color));
    @extend .flex-between;

    &__content{
        flex: 1;
    }

    &__title{
        @extend .fs-18;
        margin-bottom: 24px;
    }

    &__amount{
        margin-bottom: 0;
    }

    &__icon{
        flex-shrink: 0;
        height: 64px;
        width: 64px;
        display: grid;
        place-content: center;
        border-radius: 50%;
    }
}

.walet-balance{
    padding: 16px;
    border-radius: 8px;
    border: 1px solid hsl(var(--border-color));
    display: flex;
    align-items: flex-end;
    box-shadow: var(--sm-box-shadow);

    &__content{
        flex: 1;
    }

    &__title{
        @extend .fs-18;
        color: hsl(var(--black));
        margin-bottom: 12px;
    }

    &__amount{
        @extend .flex-align;
        gap: 16px;
        margin-bottom: clampCal(24,32);
        color: hsl(var(--base));
        .loader{
            font-size: 24px;
        }
    }

    &__withdraw{
        display: inline-flex;
        gap: 8px;
        cursor: pointer;
        color: hsl(var(--black));
        .icons{
            font-size: 24px;
            display: flex;
        }
    }

    &__right{
        flex-shrink: 0;
    }

    &__image{
        width: 56px;
        margin-left: auto;
        margin-bottom: 8px;
    }

    &__text{
        color: hsl(var(--black));
    }
}
