.contact-section{
    @include md-screen{
        padding-top: 60px;
    }
    .account-form{
        padding: clampCal(24,56);
        .btn{
            @extend .fs-18
        }
    }
}

.contact-heading{
    margin-bottom: clampCal(30,70);
    &__title{
        font-weight: 600;
        margin-bottom: 16px;
        @include md-screen{
            margin-bottom: 10px;
        }
    }
    &__desc{
        font-weight: 500;
        margin-bottom: 0;
    }
}

.info-item{ 
    display: flex;
    align-items: flex-start;
    gap: 16px;
    padding-left: 12px;
    border-left: 2px solid hsl(var(--border-color));
    &:not(:last-child){
        margin-bottom: clampCal(24,64);
    }
    
    &__icon{ 
        font-size: 24px;
        flex-shrink: 0;
    }
    &__content{
        .title{
            margin-bottom: 16px;
            @include md-screen{
                margin-bottom: 10px;
            }
        }
        .text{
            max-width: 256px;
            width: 100%;
            @extend .fs-18;
            line-height: 1.5;
        }
        .link{
            margin-top: 8px;
            line-height: 1.5;
            @extend .fs-18;
            color: hsl(var(--base));
        }
    }
}
