.countdown {
    background-color: hsl(var(--bg-color));
}

.countdown-wrapper {
    display: grid;
    grid-template-columns: repeat(5, auto);

    @include msm-screen {
        grid-template-columns: repeat(3, auto);
        gap: 12px;
    }

    @include xxsm-screen {
        grid-template-columns: repeat(1, auto);
    }

    .countdown-item {
        &__count {
            margin-bottom: 24px;
            font-weight: 400;
            text-align: center;

            @include lg-screen {
                margin-bottom: 15px;
                font-size: clampCal(32, 24);
            }
            
            @include md-screen {
                margin-bottom: 10px;
                font-weight: 600;
                font-size: clampCal(28, 20);
            }

            @include msm-screen {
                font-size: clampCal(20, 14);
                margin-bottom: 0px;
            }

            @include xxsm-screen {
                font-size: clampCal(24, 20);
            }
        }

        &__title {
            color: hsl(var(--body-color));
            margin-bottom: 0px;
            font-weight: 400;
            text-align: center;

            @include msm-screen {
                font-size: 14px;
            }
        }
    }

    .count_devide {
        width: 4px;
        height: 100%;
        background-color: hsl(var(--white));

        @include sm-screen {
            display: none;
        }
    }
}
