/* ============= Header Start Here ======================= */

.mega-menu {
    background-color: hsl(var(--white));
    padding: 20px !important;
    min-width: 1050px;
    max-width: 1050px;
    border-radius: 10px !important;
    z-index: 999;
    box-shadow: var(--box-shadow-two);
    overflow: hidden;
    top: calc(100% + 20px) !important;
    pointer-events: none;
    
    
    @include lg-screen {
        width: 100%;
        min-width: 100%;
    }
    @include md-screen {
        margin-top: 15px !important;
    }

    &__list {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
    }

    &__item {
        padding: 40px 16px;
        width: calc(33.3333333333% - 40px);

        @include lg-screen {
            width: calc(33.3333333333% - 27px);
        }

        @include md-screen {
            width: 100%;
            padding: 16px;
        }

        &:first-child {
            background-color: hsl(var(--bg-color));
        }

        &:not(:first-child) {
            margin: 0px 30px;

            @include lg-screen {
                margin: 0px 20px;
            }

            @include md-screen {
                background-color: hsl(var(--bg-color));
                margin: 0px;
            }
        }
    }

    .mega-menu-nav {
        &__item {
            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }

        &__link {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: hsl(var(--black));
            transition: 0.2s linear;

            &:hover {
                color: hsl(var(--base));
            }

            .icon {
                font-size: 20px;
            }
        }
    }

    &__flexcolumn {
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: space-between;
    }

    &__heading {
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__text {
        color: hsl(var(--black));
        max-width: 260px;
        font-size: 14px;
    }

    &__button {
        .btn--md {
            padding: 10px 24px;
        }

        @include md-screen {
            margin-top: 40px;
        }
    }
}


.navbar-brand {
    padding-top: 0;
    padding-bottom: 0;
    margin-right: 48px;

    @include xxl-screen {
        margin-right: 32px;
    }

    &.logo {
        img {
            max-width: 200px;
            width: 100%;
            max-height: 60px;

            @include lg-screen {
                max-width: 140px;
            }
        }
    }
}

/* Sidebar User Info Css */
.user-profile {
    position: relative;

    &__dropdown {
        border: 1px solid hsl(var(--border-color));
        border-radius: 8px !important;
        box-shadow: var(--sm-box-shadow);
        width: 210px;
        right: 0px !important;
        left: auto !important;

        .dropdown-item {
            display: flex;
            justify-content: space-between;
            padding: 8px 16px !important;

            .icon {
                font-size: 24px;
                display: flex;
            }

            &:hover {
                color: hsl(var(--white));
                background-color: hsl(var(--base));
            }
        }

        &.show {
            display: block !important;
            visibility: visible !important;
            opacity: 1 !important;
            transform: scale(1) !important;
        }
    }
}

.user-profile-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    position: relative;

    &__image {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        overflow: hidden;
        border: 1px solid hsl(var(--border-color));
        flex-shrink: 0;
    }

    &__content {
        flex: 1;
        padding-left: 15px;
    }

    &__name {
        margin-bottom: 0px;
    }

    &__desc {
        color: hsl(var(--body-color));
        font-size: 15px;
        cursor: pointer;
    }

}

.header {
    position: relative;
    z-index: 5;
    background-color: hsl(var(--white));
    box-shadow: var(--sm-box-shadow);

    @include md-screen {
        top: 0px;
        background-color: hsl(var(--white));
        padding: 10px 0;
        position: absolute;
        left: 0;
        right: 0;
        z-index: 999;
        max-height: 101vh;
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 5px;
            height: 5px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 0px;
        }
    }

    &.fixed-header {
        background-color: hsl(var(--white));
        position: sticky;
        transition: .3s linear;
        top: 0px;
        animation: slide-down .8s;
        width: 100%;
    }
}

@keyframes slide-down {
    0% {
        opacity: 0;
        transform: translateY(-150%);
    }

    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.navbar {
    padding: 0 !important;
}

/* ========================= Desktop Device Start ========================= */
.header {
    @media (min-width: 992px) {
        .nav-menu {
            padding-top: 0;
            padding-bottom: 0;

            .nav-item {
                padding-right: 32px;
                position: static !important;

                &:last-child {
                    padding-right: 0;
                }

                &.active {
                    .nav-link {
                        color: hsl(var(--base)) !important;

                        &::before {
                            width: 100%;
                        }
                    }
                }

                &:hover {
                    .nav-link {
                        color: hsl(var(--base)) !important;

                        &::before {
                            width: 100%;
                        }

                        .nav-item__icon {
                            transform: rotate(180deg);
                            transition: .2s;
                        }
                    }
                }

                .nav-link {
                    font-weight: 500;
                    @include fs-18;
                    color: hsl(var(--heading-color)) !important;
                    padding: 27px 0;
                    position: relative;
                    cursor: pointer;

                    @include lg-screen {
                        @include fs-15;
                    }

                    /* ======================== Style two ================ */
                    &:hover::before {
                        left: 0;
                        transition: .3s;
                    }

                    &::before {
                        position: absolute;
                        content: "";
                        right: 0;
                        bottom: 0;
                        width: 0;
                        height: 2px;
                        background-color: hsl(var(--base));
                        // transform: translateX(-50%);
                        transition: .3s;
                    }

                    .nav-item__icon {
                        transition: .3s;
                        @include fs-18;
                        margin-left: 2px;

                        @include md-screen {
                            margin-right: 6px;
                        }
                    }
                }
            }
        }

        .dropdown-menu {
            display: block;
            opacity: 0;
            transition: .3s;
            top: 100%;
            left: 0;
            padding: 0 !important;
            transform-origin: top center;
            transition: .3s;
            overflow: hidden;

            &__list {
                border-bottom: 1px solid hsl(var(--border-color));

                &:last-child {
                    border-bottom: none;
                }
            }

            &__link {
                padding: 7px 20px;
                font-weight: 500;
                @include fs-16;
                transition: .3s;

                &:focus,
                &:hover {
                    color: hsl(var(--white));
                    background-color: hsl(var(--base));

                }
            }
        }

        .nav-menu {
            .nav-item {
                &:hover {
                    .dropdown-menu {
                        visibility: visible;
                        opacity: 1;
                        top: calc(100% - 1px) !important;
                        pointer-events: visible;
                    }
                }
            }
        }

        .header-right {
            .custom--dropdown {
                position: relative;
                display: inline-block;

                &__selected {
                    background-color: transparent;
                    cursor: pointer;
                    padding: 10px;
                    font-size: 18px;
                    padding-right: 16px;
                    @include flex-align;

                    .thumb {
                        width: 30px;
                        overflow: hidden;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover !important;
                        }
                    }

                    .text {
                        width: calc(100% - 30px);
                        padding-left: 10px;
                        color: hsl(var(--static-white));
                    }

                    @include xl-screen {
                        padding-right: 15px;
                    }
                }

                &.open>.dropdown-list {
                    -webkit-transform: scale(1);
                    transform: scale(1);
                    opacity: 1;
                    visibility: visible;
                    z-index: 999 !important;
                }
            }

            .dropdown-list {
                position: absolute;
                background-color: hsl(var(--white));
                width: 160px;
                border-radius: 3px;
                -webkit-box-shadow: 0px 12px 24px rgba(21, 18, 51, 0.13);
                box-shadow: 0px 12px 24px rgba(21, 18, 51, 0.13);
                opacity: 0;
                overflow: hidden;
                transition: 0.25s ease-in-out;
                transform: scaleY(0);
                -webkit-transform-origin: top center;
                transform-origin: top center;
                top: 100%;
                margin-top: 5px;
                z-index: -1;
                visibility: hidden;
                max-height: 230px;
                overflow-y: auto !important;

                &::-webkit-scrollbar {
                    width: 4px;
                    height: 4px;
                }

                &::-webkit-scrollbar-thumb {
                    background-color: hsl(var(--black)/0.15);
                }

                &::-webkit-scrollbar-thumb {
                    background-color: hsl(var(--black)/0.3);
                }

                &__item {
                    padding: 10px 16px !important;
                    cursor: pointer;
                    -webkit-transition: 0.3s;
                    transition: 0.3s;
                    font-size: 18px;
                    @include flex-align;

                    @include lg-screen {
                        @include fs-15
                    }

                    &:hover {
                        background-color: hsl(var(--white)) !important;
                    }

                    .thumb {
                        width: 30px;
                        overflow: hidden;

                        img {
                            width: 100%;
                            height: 100%;
                            object-fit: cover !important;
                        }
                    }

                    .text {
                        width: calc(100% - 30px);
                        padding-left: 10px;
                        color: hsl(var(--heading-color));
                    }
                }
            }

            .nav-item {
                .top-button {
                    .login-registration-list {
                        &__item {
                            .btn--md {
                                padding: 10px 24px;
                                @include fs-18;

                                @include lg-screen {
                                    padding: 8px 14px;
                                    @include fs-15
                                }
                            }
                        }
                    }
                }
            }

        }


    }
}

/* ========================== Desktop Device End ========================= */

/* ============================== Small Device ======================= */
.header {
    @include md-screen {
        .nav-menu {
            margin-top: 20px;

            .nav-item {
                text-align: left;
                display: block;
                position: relative;
                margin: 0;

                &:hover .nav-link .nav-item__icon {
                    transform: rotate(0deg) !important;
                }

                .btn--md {
                    @include lg-screen {
                        padding: 8px 14px;
                        @include fs-15
                    }
                }
            }
        }

        .nav-item {
            &:first-child {
                border-bottom: none;
            }

            &:last-child>a {
                border-bottom: 0;
            }

            &.active {
                .nav-link {
                    color: hsl(var(--base));
                }
            }

            .nav-link {
                margin-bottom: 8px;
                padding: 10px 10px 10px 0 !important;
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin: 0 !important;
                border-bottom: 1px solid hsl(var(--border-color));
                color: hsl(var(--heading-color));

                &::before {
                    display: none;
                }

                &.show {
                    &[aria-expanded="true"] {
                        color: hsl(var(--base)) !important;

                        i {
                            transform: rotate(180deg);
                        }
                    }
                }
            }
        }

        .dropdown-menu {
            box-shadow: none;
            border-radius: 2px;
            width: 100%;
            padding: 0 !important;
            border: 0;
            overflow: hidden;

            li {
                .dropdown-item {
                    padding: 10px 0px;
                    font-weight: 500;
                    @include fs-16;
                    color: hsl(var(--heading-color));
                    margin-left: 20px;

                    &:hover,
                    &:focus {
                        background-color: transparent;
                    }
                }
            }
        }

        .custom--dropdown {
            position: relative;
            display: inline-block;

            &__selected {
                background-color: transparent;
                cursor: pointer;
                padding: 10px;
                font-size: 18px;
                padding-right: 16px;
                padding-left: 0px !important;
                @include flex-align;

                .thumb {
                    width: 30px;
                    overflow: hidden;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover !important;
                    }
                }

                .text {
                    width: calc(100% - 30px);
                    padding-left: 10px;
                    color: hsl(var(--static-white));
                }

                @include xl-screen {
                    padding-right: 15px;
                }
            }

            &.open>.dropdown-list {
                -webkit-transform: scale(1);
                transform: scale(1);
                opacity: 1;
                visibility: visible;
                z-index: 999 !important;
            }
        }

        .dropdown-list {
            position: absolute;
            background-color: hsl(var(--white));
            width: 160px;
            border-radius: 3px;
            -webkit-box-shadow: 0px 12px 24px rgba(21, 18, 51, 0.13);
            box-shadow: 0px 12px 24px rgba(21, 18, 51, 0.13);
            opacity: 0;
            overflow: hidden;
            transition: 0.25s ease-in-out;
            transform: scaleY(0);
            -webkit-transform-origin: top center;
            transform-origin: top center;
            top: 100%;
            margin-top: 5px;
            z-index: -1;
            visibility: hidden;
            max-height: 230px;
            overflow-y: auto !important;

            &::-webkit-scrollbar {
                width: 4px;
                height: 4px;
            }

            &::-webkit-scrollbar-thumb {
                background-color: hsl(var(--black)/0.15);
            }

            &::-webkit-scrollbar-thumb {
                background-color: hsl(var(--black)/0.3);
            }

            &__item {
                padding: 8px 16px;
                cursor: pointer;
                -webkit-transition: 0.3s;
                transition: 0.3s;
                font-size: 18px;
                border-radius: 5px;
                @include flex-align;

                @include lg-screen {
                    @include fs-15
                }

                &:hover {
                    background-color: hsl(var(--white)) !important;
                }

                .thumb {
                    width: 30px;
                    overflow: hidden;

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover !important;
                    }
                }

                .text {
                    width: calc(100% - 30px);
                    padding-left: 10px;
                    color: hsl(var(--static-white));
                }
            }
        }

    }

    .navbar-toggler {
        &.header-button {
            border-color: transparent;
            color: hsl(var(--heading-color));
            background: transparent !important;
            padding: 0 !important;
            border: 0 !important;
            border-radius: 0 !important;
            transition: .15s ease-in-out;
            width: auto;
            font-size: 2.5rem;

            &:focus {
                box-shadow: none !important;
            }

            &[aria-expanded="true"] i::before {
                content: "\f00d";
            }
        }
    }
}

@include md-screen {
    .body-overlay.show {
        visibility: visible;
        opacity: 1;
    }
}

.dropdown-menu[data-bs-popper]{
    margin-top: 0px !important;
}

/* ================================= Header Css End =========================== */
