.summery-box {
    background-color: hsl(var(--bg-color));
    padding: clampCal(20, 32);
    border-radius: 8px;

    &__title {
        font-weight: 500;
        margin-bottom: clampCal(20, 32);
    }

    .btn-outline--base {
        padding: 10px 24px;
        background-color: hsl(var(--white)) !important;
        border-width: 1px !important;
        margin-bottom: 12px;

        &:hover {
            background-color: hsl(var(--base)) !important;
        }
    }
}


.summery-block {
    &:not(:last-child){
        margin-bottom: clampCal(20, 32);
    }

    &__notetext{
        font-size: 14px
    }

    &__header {
        @extend .flex-between;
        padding-bottom: clampCal(10, 16);
        margin-bottom: clampCal(10, 16);
        border-bottom: 1px solid hsl(var(--border-color));

        .price {
            color: hsl(var(--black));
        }
    }

    .list {
        .item {
            @extend .flex-between;
            color: hsl(var(--black));
            font-weight: 500;

            &:not(:last-child) {
                margin-bottom: 8px;
            }

            &:last-child {
                color: hsl(var(--success));
            }
        }
    }

    &__total {
        @extend .flex-between;
        color: hsl(var(--black));
        padding-top: clampCal(10, 16);
        margin-top: clampCal(10, 16);
        border-top: 1px solid hsl(var(--border-color));
    }

    &__title {
        display: flex;
        align-items: center;
        gap: 12px;
        margin-bottom: clampCal(16, 24);
        flex-wrap: wrap;

        .icon {
            height: 40px;
            width: 40px;
            border-radius: 8px;
            display: grid;
            place-content: center;
            background-color: hsl(var(--base));
            color: hsl(var(--white));
            font-size: 24px;
        }

        .text {
            color: hsl(var(--black));
        }
    }

    textarea.form--control {
        height: 120px;
        padding: 12px 16px;
        color: hsl(var(--light));
        font-size: 16px;

        &::placeholder {
            font-size: 16px;
            color: hsl(var(--black) / .5);
        }

        @include md-screen {
            height: 100px;
        }
    }

    .terms-condition {
        color: hsl(var(--black));
        font-size: 14px;
        text-align: center;
        line-height: 150%;
        margin-bottom: clampCal(10, 16);

        a {
            color: hsl(var(--base));
        }
    }

    &__accordion {
        margin-bottom: clampCal(20, 32);
        .accordion-button {
            padding: 0px;
            background-color: transparent;
            box-shadow: none;
            padding-bottom: clampCal(20, 32);

            &:focus {
                border: 0;
                box-shadow: none;
            }

            color: hsl(var(--black));

            .total-price {
                color: hsl(var(--success));
                margin-left: 10px;
                font-weight: 500;
            }
        }
    }
}

.not-payment{
    text-align: center;

    &__icon{
        font-size: 50px;
        margin-bottom: 24px;
        display: flex;
        justify-content: center;
    }

    &__heading{
        margin: 0;
        font-weight: 600;
    }
}

.summery-box--border{
    background-color: transparent;
    border: 1px solid hsl(var(--border-color));
    padding: clampCal(16,24);
}

.payment-item {
    display: flex;
    align-items: center;
    gap: 16px;
    flex-wrap: wrap;
    cursor: pointer;

    &:not(:last-child) {
        margin-bottom: 24px;
    }

    .payment-check {
        height: 24px;
        width: 24px;

        input {
            height: 100%;
            width: 100%;
            cursor: pointer;
        }
    }

    &__right {
        display: flex;
        align-items: center;
        gap: 24px;
        cursor: pointer;
        img{
            width: 42px;
        }
    }
}


.payment-user-info {

    .bold {
        color: hsl(var(--black));
        font-weight: 600;
    }
    
    .text {
        @extend .fs-16;

        &:not(:last-child) {
            margin-bottom: 16px;
        }
    }
}

.payment-success {
    gap: 8px;

    &__image {
        flex-shrink: 1;
        width: 48px;
    }

    &__heading {
        flex: 1;
        margin-bottom: 0;
    }
}

.promo_inner {
    .form--control {
        color: hsl(var(--light));

        &::placeholder {
            font-size: 16px;
            color: hsl(var(--black) / .5);
        }

        &:focus {
            border-right: none;
        }
    }

    .promo_btn {
        padding: 12px 16px;
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        border: none;
        transition: 0.2s linear;
        &:hover{
            background-color: hsl(var(--base-d-100));
        }
    }
}
