.newsletter {
    &__wrapper {
        padding: 90px 40px 90px 120px;
        background-color: hsl(var(--bg-color));
        border-radius: 16px;
        position: relative;
        overflow: hidden;
        z-index: 1;
        gap: 24px;

        @include xl-screen {
            padding: 70px 20px 70px 90px;
        }

        @include lg-screen {
            padding: 70px 20px 70px 90px;
        }

        @include md-screen {
            padding: 60px 70px;
        }

        @include msm-screen {
            padding: 40px 50px;
        }

        @include xsm-screen {
            padding: 30px;
        }

        @include xxsm-screen {
            padding: 20px;
        }

        &::after {
            content: "";
            width: 816px;
            height: 816px;
            flex-shrink: 0;
            position: absolute;
            top: -25px;
            right: -255px;
            background-color: hsl(var(--base-l-100));
            z-index: -1;
            border-radius: 50%;

            @include lg-screen {
                top: -70px;
                right: -330px;
            }

            @include md-screen {
                top: -20%;
                right: -70%;
            }

            @include sm-screen {
                top: -45%;
                right: -110%;
            }

            @include msm-screen {
                top: -65%;
                right: -110%;
            }

            @include msm-screen {
                top: 0px;
                left: 0px;
                border-radius: 0px;
            }
        }

        &::before {
            content: "";
            width: 528px;
            height: 528px;
            flex-shrink: 0;
            position: absolute;
            top: -315px;
            right: 15px;
            background-color: hsl(var(--base));
            z-index: -1;
            border-radius: 50%;

            @include lg-screen {
                top: -325px;
                right: 5px;
            }

            @include md-screen {
                top: -95%;
                right: -30%;
            }

            @include sm-screen {
                top: -130%;
                right: -45%;
            }

            @include msm-screen {
                display: none;
            }
        }
    }

    .newsletter-content {
        .section-heading {
            margin-bottom: 0px;
        }

        width: 380px;

        @include lg-screen {
            width: 350px;
        }

        @include md-screen {
            width: 100%;
            display: none;
        }

        .section-heading__title {
            font-weight: 500; 
            color: hsl(var(--white));
        }

        .section-heading__desc {
            color: hsl(var(--white));
        }
    }
}

.newsletter-form {
    flex: 1;

    &__logo {
        img {
            height: 20px;
        }
    }

    &__title {
        margin: 16px 0px 24px;
        font-weight: 500;

        @include msm-screen {
            color: white;
        }
    }

    &__group {
        max-width: 438px;

        .btn--base {
            padding: 12px 16px;

            i {
                transform: scale(1.2);
                font-size: 20px;
            }

            &:hover {
                background-color: hsl(var(--base-d-100)) !important;
                border-color: hsl(var(--base-d-100)) !important;
                color: hsl(var(--white)) !important;
            }
        }

        @include lg-screen {
            max-width: 330px;
        }

        @include md-screen {
            max-width: 100%;
        }

        .form--control {
            font-size: 16px;
            border: 0px;
            color: hsl(var(--light));
            margin-left: 0px !important;


            &:focus {
                box-shadow: none;
            }

            &::placeholder {
                font-size: 16px;
                color: hsl(var(--black) / .5);
            }
        }
    }
}

.input-group>:not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(.valid-feedback):not(.invalid-tooltip):not(.invalid-feedback) {
    margin-left: 0px !important;
}
