/* ============= Footer Start Here ======================= */
.footer-area {
    background-color: hsl(var(--bg-color));
    margin-top: auto;

    &.bg-img {
        background-size: contain;
        background-position: top center;
    }
}

.footer-item {
    &__logo {
        margin-bottom: 32px;

        a {
            img {
                width: 100%;
                height: 100%;
                max-width: 190px;
                max-height: 64px;
            }
        }
    }

    &__custom {
        &:not(:last-child) {
            margin-bottom: 24px;
        }

        .footer-item__title {
            margin-bottom: 4px;
        }

        .footer-contact-menu__item {
            padding-bottom: 3px;
            a{
                text-decoration: underline;
            }
        }
    }

    &__title {
        margin-bottom: 12px;
        position: relative;
    }

    .social-list {
        margin-top: 30px;

        @include md-screen {
            margin-top: 20px;
        }

        @include msm-screen {
            margin-top: 15px;
        }
    }
}

/* Footer List Item */
.footer-menu {
    display: flex;
    flex-direction: column;

    .subscription-note {
        p {
            margin-bottom: 24px;
        }
    }

    .footer-subscribe {
        .btn {
            height: 50px;
            width: 50px;
            font-size: 24px;
            display: grid;
            place-content: center;

            &:hover {
                background-color: hsl(var(--base-d-100)) !important;
                border-color: hsl(var(--base-d-100)) !important;
                color: hsl(var(--white)) !important;
            }
        }

        .form--control {
            height: 50px;
            font-size: 14px !important;
            background-color: hsl(var(--white)) !important;

            &::placeholder {
                font-size: 16px !important;
                color: hsl(var(--black) / .5);
            }
        }
    }

    &__item {
        display: block;
        padding-bottom: 12px;

        &:last-child {
            padding-bottom: 0;
        }
    }

    &__link {
        color: hsl(var(--body-color));

        &:hover {
            color: hsl(var(--base));
            text-decoration: underline;
        }
    }
}

/* Footer Contact */
.footer-contact-menu {
    &__item {
        display: flex;
        padding-bottom: 12px;

        &:last-child {
            padding-bottom: 0;
        }

        &-icon {
            width: 15px;
            color: hsl(var(--base));
            font-size: 1.25rem; // 20px
        }

        &-content {
            width: calc(100% - 15px);
        }
    }
}

/* ============= Footer End Here ======================= */

/* ============= Bottom Footer End Here ======================= */
.bottom-footer {
    background-color: hsl(var(--bg-color));

    &__content {
        border-top: 1px solid hsl(var(--black));
        padding: 30px 0px;
    }

    &__text {
        color: hsl(var(--heading-color));
        font-size: 14px;
    }

    &__list {
        display: flex;
        align-items: center;
        gap: 24px;

        li {
            a {
                transition: all linear 0.3s;
                color: hsl(var(--heading-color));
                font-size: 14px;
                text-decoration: underline;

                &:hover {
                    color: hsl(var(--base));
                }
            }
        }
    }
}

/* =============Bottom Footer End Here ======================= */
