/* ================================= Start Rating Css Start =========================== */
.rating-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    &__item {
        padding: 0 1px;
    }
    &__text {
        color: hsl(var(--warning));
        display: block;
        i{
            display: flex;
            font-size: 16px;
        }
    }
}

.rating-count{
    color: hsl(var(--black));
}
 
/* ================================= Start Rating Css End =========================== */
