.chat{
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;

    &__heading{
        padding: 16px;
        background-color: hsl(var(--bg-color));
        border-radius: 8px 8px 0 0;
        @extend .flex-align;
        gap: 8px;
        &-title{
            font-weight: 500;
            margin-bottom: 0px;
            line-height: 1.2;
        }

        &-icon{
            i{
                font-size: 24px;
                display: block;
                color: hsl(var(--base));
            }
        }
    }

    &__body{
        padding: 16px;
        min-height: 385px;
        max-height: 385px;
        overflow-y: auto;  

        
        &::-webkit-scrollbar {
            width: 8px;
        }

        /* Track */
        &::-webkit-scrollbar-track {
            background-color: hsl(var(--bg-color));
        }
        
        /* Handle */
        &::-webkit-scrollbar-thumb {
            background: hsl(var(--border-color)); 
            border-radius: 4px;
        }

        /* Handle on hover */
        &::-webkit-scrollbar-thumb:hover {
            background: hsl(var(--base)); 
        }

        .chat-current-date{
            text-align: center;
            margin-bottom: 6px;
        }

        .message-item{
            display: flex;
            flex-direction: column;
            &.form--message{
                align-items: flex-end;
            }
            &__wrapper{
                display: flex;
                align-items: flex-end;
                gap: 8px;
                margin-bottom: 8px;
            }

            &__text{
                padding: 10px 15px;
                border-radius: 24px;
                background-color: hsl(var(--bg-color));
            }

            .chat-status{
                height: 14px;
                width: 14px;
                border-radius: 50%;
                background-color: hsl(var(--base));
                flex-shrink: 0;
            }

            .message-seen-text{
                font-size: 14px;
            }

            &__profile{
                flex-shrink: 0;
                img{
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
        }
    }

    &__footer{
        padding: 16px 16px 32px;
        &-wrapper{
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 8px;
        }
        .chat-file{
            &__icon{
                font-size: 24px;
                cursor: pointer;
            }
        }

        .chat-file{
            position: relative;
            flex-shrink: 0;
            &__input{
                appearance: none;
                position: absolute;
                height: 100%;
                width: 100%;
                opacity: 0;
                cursor: pointer;
                z-index: 9;
                font-size: 0;
            }
            
            &__icon{
                pointer-events: none;
                font-size: 24px;
            }
        }
    }

    &__box{
        flex: 1;
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        gap: 24px;
        .form--control{
            padding: 16px;
            padding-right: 58px;
            font-size: 16px;
            color: hsl(var(--light));
            &::placeholder{
                font-size: 16px;
                color: hsl(var(--black) / .5);
            }
        }

        &-icon{
            position: absolute;
            top: 0px;
            right: 0px;
            height: 100%;
            width: 48px;
            font-size: 26px;
            color: hsl(var(--base));
        }
    }
}
