/* ================================= Blog Section Css Start Here ============================= */
.blog-item {
    border-radius: 8px;
    overflow: hidden;
    height: 100%;

    &:hover {
        .blog-item {
            &__thumb {
                img {
                    transform: scale(1.1);
                }
            }
        }
    }

    &__thumb {
        overflow: hidden;
        border-radius: 8px;
        max-height: 300px;

        &-link {
            width: 100%;
            height: 100%;

            img {
                transition: .2s linear;
            }
        }
    }

    &__content {
        background-color: hsl(var(--white)/.07);
        padding-top: 24px;
        height: 100%;

        @include sm-screen {
            padding-top: 20px;
        }

        @include xsm-screen {
            padding-top: 16px;
        }
    }

    &__title {
        margin: 15px 0px;
        line-height: 1.4;
        font-weight: 500;

        @include msm-screen {
            margin-top: 15px;
        }

        &-link {
            color: hsl(var(--heading-color));
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;

            &:hover {
                color: hsl(var(--base));
            }
        }
    }

    &__subtitle {
        color: hsl(var(--base));
        font-size: 14px;
        font-weight: 600;
    }

    &__desc {
        margin-bottom: 25px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;

        @include sm-screen {
            margin-bottom: 15px;
        }
    }

    &__author {
        display: flex;
        gap: 16px;

        &-thumb {
            flex-shrink: 0;

            img {
                height: 48px;
                width: 48px;
                display: block;
                border-radius: 50%;
                object-fit: cover;
            }
        }

        &-info {
            flex: 1;

            &__name {
                margin-bottom: 3px;
                font-size: clampCal(16, 14);
            }

            .flex-align {
                gap: 8px;
            }

            &__date,
            &__time,
            &__divide {
                font-size: 14px;
                color: hsl(var(--heading-color));
            }
        }
    }
}

/* Text Border Bottom Animation Css Start */
.border-effect {
    display: inline !important;
    width: 100%;
    background-repeat: no-repeat;
    background-position-y: -2px;
    background-image: linear-gradient(transparent calc(100% - 1px), currentColor 1px);
    -webkit-transition: .6s cubic-bezier(.215, .61, .355, 1);
    -o-transition: .6s cubic-bezier(.215, .61, .355, 1);
    transition: .6s cubic-bezier(.215, .61, .355, 1);
    background-size: 0 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.border-effect:hover {
    background-image: linear-gradient(transparent calc(100% - 1px), currentColor 1px);
    background-size: 100% 100%;
}

/* Text Border Bottom Animation Css Ends */
