/* ================================ Testimonails Section Css Start ============================= */
.testimonials {
    position: relative;
    overflow: hidden;
}

.testimonial-slider {
    margin: 0px -80px;

    @include xxxl-screen {
        margin: 0px -50px;
    }

    @include md-screen {
        margin: 0px -30px;
    }

    @include sm-screen {
        margin: 0px -10px;
    }
}

.testimonial-item {
    border-radius: 5px;
    position: relative;
    height: 100%;
    padding: 0px 40px;

    @include xxxl-screen {
        padding: 0px 20px;
    }

    @include sm-screen {
        padding: 0px 10px;
    }

    &__info {
        margin-top: 48px;

        @include sm-screen {
            margin-top: 24px;
        }

        @include md-screen {
            margin-top: 32px;
        }
    }

    &__details {
        text-align: center;
    }

    &__name {
        margin-bottom: 6px;
        color: hsl(var(--heading-color));
        font-weight: 500;
    }

    &__desc {
        color: hsl(var(--heading-color));
        text-align: center;
        font-size: 20px;
        font-weight: 500;

        @include lg-screen {
            font-size: 18px;
        }

        @include xxsm-screen {
            font-size: 16px;
        }
    }
}

.lightcase-icon-spin:before {
    font-family: 'Line Awesome Free';
    font-weight: 900;
    content: "\f1ce";
}

.lightcase-icon-close:before {
    content: "\f00d";
    font-family: 'Line Awesome Free';
    font-weight: 900;
}

.video-container {
    width: 100%;
    height: 630px;
    border-radius: 16px;
    overflow: hidden;
    position: relative;

    &__image {
        height: 100%;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            background: linear-gradient(0deg, rgba(35, 35, 35, 0.32) 0%, rgba(35, 35, 35, 0.32) 100%);
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__preview {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 140px;
        height: 110px;
        border: 2px solid hsl(var(--white));
        border-radius: 6px;
        display: grid;
        place-content: center;
        font-size: 48px;
        color: hsl(var(--white));
        background-color: hsl(var(--white) / .3);

        @include lg-screen {
            width: 120px;
            height: 100px;
            font-size: 44px;
        }

        @include md-screen {
            width: 110px;
            height: 80px;
            font-size: 40px;
        }

        @include sm-screen {
            width: 90px;
            height: 60px;
            font-size: 30px;
        }

        @include xsm-screen {
            width: 80px;
            height: 50px;
        }

        @include xxsm-screen {
            width: 60px;
            height: 40px;
            font-size: 18px;
        }
    }

    &__text {
        color: hsl(var(--white));
        position: absolute;
        bottom: 11%;
        width: calc(100% - 50px);
        left: 50%;
        transform: translateX(-50%);
        text-align: center;
        font-weight: 500;
        margin-bottom: 0px;

        @include xsm-screen {
            font-size: 18px;
        }
    }

    @include lg-screen {
        height: 500px;
    }

    @include md-screen {
        height: 400px;
    }

    @include sm-screen {
        height: 300px;
    }

    @include xsm-screen {
        height: 240px;
    }

    @include xxsm-screen {
        height: 200px;
    }
}

/* ================================ Testimonails Section Css End ============================= */
