.signup {
    .form-group {
        text-align: center;
    }

    .btn--form {
        max-width: 335px;
        margin-top: 34px;
        margin-bottom: 8px;
    }

    .account-form__title {
        margin-bottom: clampCal(24, 50);
    }
}

.signup-wrapper {
    max-width: 810px;
    margin: 0px auto;

    .signstep-item {
        border-radius: 8px;
        border: 1px solid hsl(var(--border-color));
        padding: 24px;
        position: relative;
        height: 100%;
        display: block;
        transition: 0.2s linear;

        &.active {
            border-color: hsl(var(--base));
            background-color: hsl(var(--base) / .1);
        }

        @include sm-screen {
            margin-bottom: 24px;
            height: auto;
        }

        &__check {
            text-align: right;

            &-input {
                cursor: pointer;

                &:checked,
                &:not(:checked) {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    height: 100%;
                    width: 100%;
                    appearance: none;
                    z-index: 1;
                }


                &:checked+label:after,
                &:not(:checked)+label:after {
                    content: '';
                    position: absolute;
                }

                &:checked+label::before {
                    border-color: hsl(var(--base));
                }

                &:not(:checked)+label:after {
                    opacity: 0;
                    transform: translate(-50%, -50%) scale(0);
                }
            }

            label {
                height: 24px;
                width: 24px;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 100%;
                    border: 2px solid hsl(var(--border-color));
                    border-radius: 100%;
                    background: hsl(var(--white));
                    transition: .2s linear;
                }

                &::after {
                    height: calc(100% - 10px);
                    width: calc(100% - 10px);
                    top: 50%;
                    left: 50%;
                    transition: 0.2s linear;
                    border-radius: 50%;
                    background: hsl(var(--base));
                    transform: translate(-50%, -50%);
                }
            }
        }

        &__icon {
            margin-top: -24px;
            margin-bottom: 24px;
            max-width: 64px;
            width: 100%;
        }

        &__title {
            color: hsl(var(--heading-color));
            font-weight: 400;
            margin-bottom: 0px;
        }
    }
}
