.recent-works {
    @include md-screen{
        margin-top: 60px;
    }
    &__wrapper {
        padding: 24px 16px;
        border-radius: 8px;
        border: 1px solid hsl(var(--border-color));
    }

    &__top{
        margin-bottom: 24px;
    }
}

.work-item {
    padding: 16px;
    border-radius: 6px;
    border: 1px solid hsl(var(--border-color));
    transition: all 0.3s linear;
    &:hover{
        background-color: hsl(var(--bg-color));
    }

    &__top {
        margin-bottom: 16px;
        padding-bottom: 16px;
        border-bottom: 1px solid hsl(var(--border-color));
    }

    &__title {
        font-weight: 500;
    }

    &__price {
        font-weight: 500;
        color: hsl(var(--success));
    }

    &__subTitle {
        @extend .fs-18;
        color: hsl(var(--base));
        font-weight: 500;
    }

    &__body {
        margin-bottom: 16px;
    }

    .work-itemlist {
        display: flex;
        flex-direction: column;
        gap: 12px;
        max-width: 350px;

        &__item {
            .bold {
                color: hsl(var(--black));
                font-weight: 500;
            }
        }

        &__shedule {
            display: flex;
            gap: 12px 32px;
            flex-wrap: wrap;

            &-item {
                .text {
                    color: hsl(var(--base));
                    font-weight: 500;
                }
            }
        }
    }
    &__button{
        button{
            padding: 8px 24px;
            line-height: 1.5;
        }
    }
}

.profile-card{
    border-radius: 8px;
    border: 1px solid hsl(var(--border-color));
    box-shadow: var(--sm-box-shadow);
    padding: 16px;
    position: sticky;
    top: 100px;

    @include md-screen{
        position: static;
    }

    &__header{
        @extend .flex-between;
        gap: 6px;
        margin-bottom: 8px;
    }

    &__title{
        color: hsl(var(--success));
        font-weight: 600;
        margin-bottom: 0;
    }

    &__complete{
        margin-bottom: 12px;
        .count{
            color: hsl(var(--black));
        }
    }

    &__link{
        color: hsl(var(--base));
        font-weight: 600;
        text-decoration: underline;
    }

    &__refresh{
        font-size: 24px;
        cursor: pointer;
    }
}
