/* ================================= Pagination Css Start =========================== */

.table-show-text{
    @extend .fs-14;
}

.pagination {
    flex-wrap: wrap;
    justify-content: flex-end;
    .page-item {
        &.active {
            .page-link {
                background-color: hsl(var(--base));
                color: hsl(var(--white));
                border-color: hsl(var(--base));
            }
        }

        &:first-child,
        &:last-child{
            .page-link {
                background-color: hsl(var(--bg-color));
            }
        }

        .page-link {
            border: none;
            margin: 0 4px;
            border-radius: 4px;
            height: 28px;
            width: 28px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: transparent;
            font-weight: 400;
            @extend .fs-14;
            padding: 0;
            color: hsl(var(--black));
            &:hover {
                background-color: hsl(var(--base));
                color: hsl(var(--white));
                border-color: hsl(var(--base));
            }
            &:focus {
                box-shadow: none;
            }
            &.active{
                background-color: hsl(var(--bg-color));
                color: hsl(var(--base));
                font-weight: 500;
            }

        }
    }
}
/* ================================= Pagination Css End =========================== */
