.allservice{
    padding-top: 0px;
    @include md-screen{
        padding-top: 60px;
    }
    .section-heading{
        margin-bottom: 0;
        gap: 24px;
    }
}

.search-inner{
    max-width: 296px;
    width: 100%;
    position: relative;
    .form--control{
        color: hsl(var(--light));
        font-size: 16px;
        padding-right: 50px;
        &::placeholder{
            color: hsl(var(--light));
            font-size: 16px;
        }
    }
    .search-icon{
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 50px;
        display: grid;
        place-content: center;
        transform: scale(1.6) rotate(-90deg);
        color: hsl(var(--base));
    }
}
