/* =========================== Banner Section Start Here ========================= */
.banner-section {
    position: relative;
    z-index: 1;
    padding: 80px 0;
    background-color: hsl(var(--base));
    min-height: calc(100vh - 80px);
    display: flex;
    align-items: center;
    overflow: hidden;

    @include lg-screen {
        min-height: auto;
    }

    @include md-screen {
        padding: 140px 0 80px;
        min-height: auto;
    }

    @include sm-screen {
        padding: 110px 0 50px;
    }
}

.banner-content {
    position: relative;


    @include xxl-screen {
        padding-right: 60px;

        .banner-content__title {
            font-size: clampCal(28, 46);
        }
    }

    @include md-screen {
        padding-right: 0px;
    }

    &__shape {
        position: absolute;
        top: 36%;
        right: 0;
        height: 130px;
        animation: bannerShape linear 5s infinite;
        z-index: -1;

        @include xl-screen {
            top: 20%;
        }

        @keyframes bannerShape {
            0% {
                transform: scale(1);
            }

            50% {
                transform: scale(1.2);
            }

            100% {
                transform: scale(1);
            }
        }

        img {
            height: 100%;
        }
    }


    &__title {
        color: hsl(var(--white));
        font-weight: 600;
    }

    &__desc {
        max-width: 670px;
        color: hsl(var(--white));
        margin: 24px 0 40px;
        font-weight: 400;
        @include fs-20;

        @include lg-screen {
            margin: 20px 0 36px;
        }
    }

    &__search-area {
        max-width: 672px;
        width: 100%;
        .btn{
            margin: 0px !important;
            &:active{
                top: 0px;
                border-color: hsl(var(--base));
            }
        }
    }

    .input-group {
        border: 1px solid hsl(var(--white));
        border-radius: 8px;

        .form--control {
            font-size: 20px;
            border: none;
            border-radius: 5px;
            &::placeholder{
                color: hsl(var(--black) / .5);
                font-weight: 500;
            }
        }

        .input-group-text {
            padding: 17px 24px;
            i{
                transform: scale(1.5) rotate(265deg);
            }
            @include lg-screen {
                padding: 16px 20px;
            }
        }
    }
}

.trusted-client {
    margin: 40px 0px;

    @include lg-screen {
        margin: 36px 0px;
    }

    &__title {
        color: hsl(var(--white));
        font-weight: 400;
        @include fs-20;
        margin-bottom: 16px;
    }

    &__wrapper {
        gap: 24px;
    }

    .trusted-item {
        img {
            height: 32px;
        }
    }
}

.customer-wrapper {
    padding-left: 20px;
    &__item {
        height: 57px;
        width: 57px;
        background: hsl(var(--white));
        border-radius: 50%;
        border: 2px solid hsl(var(--white));
        overflow: hidden;
        margin-left: -22px;
        display: grid;
        place-content: center;

        img {
            height: 100%;
            width: 100%;
        }

        .customer-count {
            color: hsl(var(--base));
            text-align: center;
            line-height: .8;
        }
    }
}

.customer-wrapper-title {
    color: hsl(var(--white));
    font-weight: 400;
    @include fs-20;
}

.banner-thumb {
    position: relative;
    right: -200px;
    z-index: 2;

    @include md-screen {
        text-align: center;
    }

    @media screen and (max-width: 1699px) {
        right: -70px;
    }

    @include xxxl-screen {
        right: 0px;
    }

    &__image{   
        position: relative;
        z-index: 1;     
        img {
            transform: scale(1.3);
            width: 100%;
    
            @include xxl-screen {
                transform: scale(1.3);
            }
        }
    }

    &::after {
        content: "";
        height: 460px;
        width: 460px;
        background: var(--shape-gradient);
        border-radius: 50%;
        position: absolute;
        top: -96px;
        right: -30px;
        z-index: -1;

        @include xxxl-screen {
            height: 420px;
            width: 420px;
        }

        @include xl-screen {
            height: 340px;
            width: 340px;
        }

        @include lg-screen {
            height: 240px;
            width: 240px;
            right: -10px;
        }

        @include md-screen {
            right: 50px;
            top: 0px;
        }
    }

    &::before {
        content: "";
        position: absolute;
        width: 216px;
        height: 216px;
        transform: rotate(-90deg);
        background: var(--shape-gradient);
        border-radius: 50%;
        bottom: 70px;
        z-index: -1;
        left: -60px;
    }
}

/* =========================== Banner Section End Here ========================= */
