.profile {
    .form--control {
        font-size: 16px;
        color: hsl(var(--light));
        padding: 13px 16px;

        &::placeholder {
            font-size: 16px;
            color: hsl(var(--black) / .5);
        }
    }
}

.profile-block {
    &__title {
        font-weight: 500;
        margin-bottom: clampCal(24, 32);
    }

    &__heading {
        font-weight: 500;
        margin-bottom: 16px;
    }

    &__note {
        color: hsl(var(--black));
        max-width: 486px;
    }
}

.profile-wrapper {
    max-width: 730px;
    width: 100%;
}

.profile-wrapper--lg {
    max-width: 870px;
    width: 100%;
}

.profile-header {
    &__title {
        margin-bottom: 0;
        font-weight: 500;
    }
}

.profile-info {
    display: flex;
    gap: 40px;
    align-items: flex-start;

    @include msm-screen {
        flex-direction: column;
        gap: 24px 0;
    }
}

.profile-info-content {
    flex: 1;
}

.profile-name {
    color: hsl(var(--black));
    font-weight: 600;
    margin-bottom: 8px;
    line-height: 1.5;
}

.profile-contact-info {
    font-size: 14px;
    line-height: 1.5;
}

.profile-designation {
    margin-top: clampCal(16, 24);
    font-weight: 600;
    font-size: 14px;
    color: hsl(var(--black));
    margin-bottom: 8px;
}

.profile-overview {
    color: hsl(var(--black));
    margin-bottom: 16px;
}

.profile-rating {
    padding: 8px 16px;
    border-radius: 6px;
    border: 1px solid hsl(var(--border-color));
    box-shadow: var(--sm-box-shadow);
    display: flex;
    gap: 6px;
    align-items: center;

    &__count {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.5;
    }

    &__icon {
        font-size: 20px;
        color: hsl(var(--warning));
        display: flex;
    }
}

.profile-badge {
    background-color: hsl(var(--success));
    padding: 8px 16px;
    border-radius: 6px;
    display: flex;
    gap: 6px;
    color: hsl(var(--white));
    align-items: center;

    &__icon {
        font-size: 20px;
        display: flex;
    }

    &__text {
        font-size: 14px;
        font-weight: 600;
        line-height: 1.5;
    }
}

.profile-image-inner {
    position: relative;
    display: inline-block;
    flex-shrink: 0;

    .profile-image {
        img {
            width: 150px;
            height: 150px;
            border-radius: 50%;
            object-fit: cover;
            border: 1px solid hsl(var(--border-color));
        }
    }

    .profile-image-upload {
        position: absolute;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: hsl(var(--base));
        bottom: 0px;
        right: 0px;

        input {
            appearance: none;
            position: absolute;
            height: 100%;
            width: 100%;
            opacity: 0;
            cursor: pointer;
            z-index: 9;
            font-size: 0;
        }

        label {
            position: absolute;
            height: 100%;
            width: 100%;
            display: grid;
            place-content: center;
            color: hsl(var(--white));
            font-size: 20px;
        }
    }
}

.offer-card {
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid hsl(var(--border-color));

    &__cupon {
        margin-bottom: 16px;
        color: hsl(var(--white));
    }

    &__title {
        font-weight: 600px;
        color: hsl(var(--white));
        margin-bottom: 0px;
    }

    .offer-card-body {
        padding: 32px 24px;

        @include lg-screen {
            padding: 24px 16px;
        }
    }

    &__text {
        line-height: 1.5;
        color: hsl(var(--black));
        margin-bottom: 16px;
    }

    &__list {
        margin-bottom: clampCal(24, 32);
    }

    &__listitem {
        display: flex;
        align-items: center;
        gap: 4px;

        .icon {
            font-size: 24px;
            color: hsl(var(--base));
        }

        .list-text {
            color: hsl(var(--black));

            .base {
                color: hsl(var(--base));
            }
        }
    }

    .offer-card-button {
        .btn--md {
            padding: 10px 24px;
            border-width: 1px !important;
        }
    }
}


.offer-card-top {
    text-align: center;
    background: linear-gradient(90deg, hsl(var(--base-l-300)) 0%, hsl(var(--base)) 100%);
    padding: 44px;
    position: relative;
    z-index: 1;

    @include lg-screen {
        padding: 32px 24px;
    }

    &::after {
        content: "";
        position: absolute;
        height: 120px;
        width: 120px;
        top: -16px;
        right: -18px;
        background-image: url(../images/icons/offershape.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        z-index: -1;
        opacity: 0.1;
    }

    &::before {
        content: "";
        position: absolute;
        height: 120px;
        width: 120px;
        bottom: -35px;
        left: -22px;
        background-image: url(../images/icons/offershapeone.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        z-index: -1;
        opacity: 0.1;
    }
}


.promo-item {
    display: flex;
    align-items: center;
    gap: 16px;

    @include msm-screen {
        flex-direction: column;
        align-items: flex-start;
    }

    &__image {
        flex-shrink: 0;

        img {
            width: 100px;

            @include msm-screen {
                width: 80px;
            }
        }
    }

    &__content {
        flex: 1;
    }

    &__title {
        margin-bottom: 8px;
    }
}
