/* =========================================== Account Css Start =========================*/
.acccount-header{
    padding: 22px 0px;
    margin-bottom: 32px;
    
    &__logo{
        height: 37px;
        @include lg-screen{
            max-width: 140px;
            height: auto;
        }
    }

    &__content{
        font-size: 14px;
    }

    &__title{
        color: hsl(var(--heading-color));
    }

    &__link{ 
        color: hsl(var(--base));
    }
}

.account-inner{
    position: relative;
}

.login-wrapper{
    max-width: 540px;
    width: 100%;
    margin: 0px auto;
}

.account-form  {
    box-shadow: var(--sm-box-shadow);
    background-color: hsl(var(--white));
    border: 1px solid hsl(var(--border-color));
    padding: clampCal(25 , 50) clampCal(24 , 68);
    border-radius: 16px;
    overflow: hidden;

    &__title{
        margin-bottom: clampCal(18 , 32);
        text-align: center;
        font-weight: 500;
    }

    .have-account{
        &__text{
            font-size: 14px;
            color: hsl(var(--heading-color));
        }
    }

    &__external{
        a{
            display: flex;
            justify-content: center;
            align-items: center;
            &:not(:last-child){
                margin-bottom: 12px;
            }
        }

        .icon{
            margin-right: 16px;
        }
    }
    
    .btn--form{
        font-size: clampCal(14,18);
    }
    

    &__divide{
        position: relative;
        z-index: 1;
        .text{
            display: inline-block; 
            font-size: 14px;
            color: hsl(var(--heading-color));
            padding-inline:  8px;
            background-color: hsl(var(--white));
        }
        &::after{
            position: absolute;
            content: "";
            width: 100%;
            height: 1px;
            background-color: hsl(var(--border-color));
            top: calc(50% + 1px);
            left: 0px;
            z-index: -1;
        }
    }

    .form--control{
        color: hsl(var(--light));
        line-height: 1.25;
        border-radius: 8px;
        border-color: hsl(var(--border-color));
        font-weight: 500;
        padding-inline: 12px;
        font-size: clampCal(14,16);
        &::placeholder{
            font-size: clampCal(14,16);
            color: hsl(var(--black) / .5);
        }
    }

    .login-account-text{
        font-size: clampCal(14, 18);
        text-align: center;
        font-weight: 500;
        color: hsl(var(--heading-color));
        padding-top: 16px;
        @include md-screen{
            padding-top: 0px;
        }
    }

    .login-back-btn{
        max-width: 230px;
        margin: 0 auto;
    }

}
/* ============================================ Account Css End ====================================*/
