.professional-benifits {
    &__wrapper {
        background-color: hsl(var(--base));
        border-radius: 16px;
        overflow: hidden;
    }

    &__image {
        height: 100%;

        img {
            height: 100%;
            object-fit: cover;
        }
    }
}

.professional-benifits-content {
    padding: 48px 48px 48px 24px;

    @include lg-screen {
        padding: 40px 24px;
    }

    @include xsm-screen {
        padding: 32px 24px;
    }

    .section-heading {
        &__title {
            color: hsl(var(--white));
        }

        &__desc {
            color: hsl(var(--white));
            font-size: 18px;
        }

        &__subtitle {
            color: hsl(var(--white));
            font-weight: 500;
        }
    }

    &__button {
        margin-top: 40px;
    }
}


.professional-benifits-list {
    margin-top: 95px;
    gap: 24px 0px;

    @include lg-screen {
        margin-top: 30px;
    }

    @include xl-screen {
        margin-top: 50px;
    }
}

.professional-benifits-item {
    width: calc(100% / 3);

    @include msm-screen {
        width: calc(100% / 2);
    }

    @include xxsm-screen {
        width: 100%;
    }


    &__image {
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid hsl(var(--white) / .5);

        @include msm-screen {
            margin-bottom: 15px;
            padding-bottom: 15px;
        }

        img {
            height: 64px;
            width: 64px;
        }
    }

    &__title {
        margin-bottom: 0px;
        font-weight: 400;
        color: hsl(var(--white));
        padding-right: 20px;
    }
}
