
.service-complete{
    @include md-screen{
        margin-top: 60px;
    }
    &__wrapper{
        max-width: 910px;
        width: 100%;
        margin: 0px auto;
        padding: 105px 40px;
        border-radius: 8px;
        background-image: url(../images/thumbs/complete.png);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        border: 1px solid hsl(var(--border-color));

        @include md-screen{
            padding: 80px 30px;
        }

        @include sm-screen{
            padding: 50px 20px;
        }
    }
    
    &__content{
        max-width: 440px;
        width: 100%;
        margin: 0px auto;
    }
    
    &__image{
        width: 135px;
        margin: 0px auto clampCal(16,24);
        @include md-screen{
            width: 100px;
        }
        img{
            width: 100%;
        }
    }
    
    &__title{
        text-align: center;
        margin: 0px auto clampCal(24,40);
    }

   .btn--md{
        padding: 10px 24px;
        @include xxsm-screen{
            width: 100%;
        }
   }
}
