.solution-thumb {
    img {
        max-width: 475px;
        width: 100%;
    }

    background-color: hsl(var(--bg-color));
    padding: 87px 30px 40px;
    text-align: center;
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;

    @include msm-screen {
        padding: 30px 15px;
    }
}

.solution-content {
    &__btn {
        margin-top: 48px;
        gap: 18px;
        
        @include lg-screen{
            margin-top: 30px;
        }
        
        @include msm-screen {
            gap: 12px;
        }
        
        .btn--md {
            @include lg-screen {
                font-size: 16px;
                padding: 14px 12px;
            }
            
            @include lg-screen {
                @include fs-16;
                padding: 14px 12px;
            }
            
            @include xsm-screen {
                @include fs-14;
                padding: 14px 12px;
            }
            
            @include xxsm-screen {
                width: 100%;
            }
            
            .icon {
                margin-left: 16px;
                
                @include msm-screen {
                    margin-left: 6px;
                }
            }
        }
    }
}

.solution-item {
    display: flex;
    align-items: flex-start;
    gap: 24px;

    &:not(:last-child) {
        margin-bottom: 40px;

        @include xl-screen {
            margin-bottom: 20px;
        }
    }

    &__icon {
        flex-shrink: 0;
        font-size: 30px;
        color: hsl(var(--heading-color));
        display: flex;
    }

    &__title {
        margin: 0px 0px 16px;
        font-weight: 500;

        @include lg-screen{
            margin: 0px 0px 10px;
        }

        @include md-screen{
            font-weight: 600;
        }
    }
}

