
.blog-detials{
    .blog-info{
        &__title{
            font-weight: 500;
            margin-block: clampCal(18,32);
        }

        &__date{
            @extend .fs-16;
            color: hsl(var(--black));
            font-weight: 500;
        }
    }

    .blog-banner{
        padding-left: 24px;
        @include md-screen{
            padding-left: 0;
        }
        &__image{ 
            border-radius: 8px;
            overflow: hidden;
        }
    }

    .blog-sidebar{
        position: sticky;
        top: 90px;
        @include md-screen{
            position: static;
        }
        .blog-author{
            .flex-align{ 
                gap: 16px;
            }
            &__title{
                margin-bottom: clampCal(14, 24);
            }
            &__iamge{
                img{
                    height: 56px;
                    width: 56px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
            &__name{
                font-weight: 600;
                color: hsl(var(--black));
            }

            &__designation{
                color: hsl(var(--black));
                font-weight: 500;
            }
        }

        &__divide{
                margin-block: 48px;
                border-color: hsl(var(--border-color));
                opacity: 1;
        }

        .subscribe-form{
            display: flex;
            flex-direction: column;
            gap: 16px;
            .form--control{
                color: hsl(var(--light));
                &::placeholder{
                    font-size: 16px;
                    color: hsl(var(--black) / .5);
                }
            }

            .policy-text{
                font-size: 12px;
                color: hsl(var(--heading-color));
                font-weight: 500;
                .text{
                    text-decoration: underline;
                    color: hsl(var(--heading-color));
                }
            }
        }

        .blog-share{
            .title{
                margin-bottom: clampCal(10,16);
            }

            .social-share{
                ul{
                    gap: 8px;
                }

          
                &__link{
                    height: 32px;
                    width: 32px;
                    background-color: hsl(var(--section-bg));
                    display: grid;
                    place-content: center;
                    border-radius: 50%;
                    @extend .fs-20;
                    color: hsl(var(--base));
                    transition: all linear 0.2s;
                    &:hover{
                        background-color: hsl(var(--base));
                        color: hsl(var(--white));
                    }
                }
                
                
            }
        }
    }

    .blog-content{
        padding-left: 24px;
        @include md-screen{
            padding-left: 0;
        }
        &__title{
            font-weight: 500;
            margin-bottom: clampCal(15, 24);
        }
        .text{
            color: hsl(var(--black));
            font-weight: 500;
            margin-bottom: 16px;
        }

        .blog-image-wrapper{
            padding-block: clampCal(24, 48);
            img{
                border-radius: 8px;
                overflow: hidden;
            }

            .blog-image-caption{
                margin-top: clampCal(10,16);
                @extend .fs-14;
                color: hsl(var(--black));
                font-weight: 500;
                padding-left: 8px;
                border-left: 2px solid hsl(var(--black));
                display: block;
            }
        }

        .heading-text{
            font-weight: 600;
            line-height: 1.5;
            padding-top: clampCal(15,20);
            padding-bottom: clampCal(10,16);
        }

        .quate-text{
            padding-left: clampCal(16,24);
            border-left: 2px solid hsl(var(--black));
            font-style: italic;
            font-weight: 500;
            line-height: 1.5;
            margin-top: clampCal(22, 36);
            margin-bottom: clampCal(20, 32);
        }
    }
}
