.review-conetnt {
    box-shadow: var(--sm-box-shadow);
    padding: 32px;
    border-radius: 8px;
    border: 1px solid hsl(var(--border-color));
    max-width: 910px;
    width: 100%;
    margin: 0px auto;

    @include md-screen {
        padding: 24px;
    }

    @include sm-screen {
        padding: 16px;
    }

    .section-heading {
        &__title {
            font-weight: 600;
        }
    }

    .provider-profile {
        padding: 0;
        box-shadow: none;
        border: 0;
    }

    .review-block {
        &__title {
            margin-bottom: clampCal(16, 24);
            font-weight: 600;
        }

        &:not(:last-child) {
            margin-bottom: clampCal(24, 40);
        }
    }

    .product-card {
        display: flex;
        padding: 0;
        border: 0;
        box-shadow: none;
        gap: 16px;

        @include xxsm-screen {
            flex-direction: column;
        }

        &__heading {
            margin-top: 0px;
        }

        &__text {
            margin-bottom: 8px;
        }

        &__price {
            margin: 0px;
        }

        &__image {
            flex-grow: 0;

            img {
                width: 147px;
                height: 107px;
                border-radius: 8px;
                object-fit: cover;

                @include xxsm-screen {
                    width: 100%;
                    height: auto;
                }
            }
        }

        &__content {
            padding: 0;
        }
    }
}

.review {
    .btn--md {
        border-width: 1px !important;
        padding: 10px 24px;
    }
}

.review-form {
    padding: 24px;
    border-radius: 8px;
    background-color: hsl(var(--bg-color));
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include sm-screen {
        gap: 16px;
        padding: 16px;
    }

    &__title {
        margin: 0;
        font-weight: 500;
    }

    textarea.form--control {
        height: 120px;
        font-size: 16px;
        padding: 12px 16px;

        &::placeholder {
            font-size: 16px;
            color: hsl(var(--black) / .5);
        }

        @include sm-screen {
            height: 90px;
        }
    }
}


.rating_wrapper {
    .rating__label {
        cursor: pointer;
        padding: 0 0.1em;
        font-size: 16px;
    }

    .rating__icon--star {
        color: hsl(var(--warning));
        font-weight: 700;
    }

    .rating__input {
        position: absolute !important;
        left: -9999px !important;

        &:checked~ {
            .rating__label {
                .rating__icon--star {
                    color: hsl(var(--warning));
                    font-weight: 500;
                }
            }
        }
    }

    .rating-group {
        display: -webkit-inline-box;
        display: -ms-inline-flexbox;
        display: inline-flex;

        &:hover {
            .rating__label {
                .rating__icon--star {
                    color: hsl(var(--warning));
                    font-weight: 700;
                }
            }
        }
    }

    .rating__input {
        &:hover~ {
            .rating__label {
                .rating__icon--star {
                    font-weight: 500;
                }
            }
        }
    }
}
