/* ================================= Dashboard Fulid Sidebar Css Start =========================== */
.sidebar-menu {
  width: 280px;
  position: sticky;
  top: 90px;

  &.show-sidebar {
    left: 0px;
  }

  &__close{
    position: absolute;
    top: 10px;
    right: -50px;
    background-color: hsl(var(--base));
    color: hsl(var(--white));
    height: 32px;
    width: 32px;
    border-radius: 50%;
    display: none;

    @include md-screen{
      display: grid;
      place-content: center;
    }

    @include xxsm-screen{
      right: -36px;
    }

    .icon{
      font-size: 20px;
    }
  }

  @include md-screen {
    position: fixed;
    top: 0px;
    left: -100%;
    height: 100vh;
    background-color: hsl(var(--white));
    z-index: 1025;
    transition: all linear 0.3s;

    &::-webkit-scrollbar {
      width: 0px;
    }
  }
}

.sidebar-menu-list {
  @include md-screen{
    height: 100%;
    overflow-y: auto;
  }

  &__link {
    &.active {
      background-color: hsl(var(--bg-color));
      color: hsl(var(--base));
      border-right: 5px solid hsl(var(--base));
    }

    &:hover {
      background-color: hsl(var(--bg-color));
      color: hsl(var(--base));
      border-right: 5px solid hsl(var(--base));
    }

    padding: 16px 12px;
    border-right: 5px solid transparent;
    font-size: 18px;
    color: hsl(var(--black));
    font-family: var(--heading-font);
    line-height: 1.3;
    font-weight: 500;
    display: block;
    transition: all linear 0.3s;

    @include md-screen {
      padding: 12px 24px;
      font-size: 16px;
    }
  }
}

.right-body {
  .dashboard-body__bar {
    margin-bottom: 12px;
    &-icon {
      background-color: hsl(var(--base));
      height: 36px;
      width: 36px;
      border-radius: 8px;
      display: grid;
      place-content: center;
      font-size: 22px;
      font-weight: 100;
      color: hsl(var(--white));
    }
  }

  width: calc(100% - 280px);
  padding-left: 60px;
  border-left: 1px solid hsl(var(--border-color));

  @include md-screen {
    border-left: none;
    padding-left: 0px;
    width: 100%;
  }
}

/* ================================= Dashboard Fulid Sidebar Css End =========================== */
