/* ================ Top Header Start Here ================ */
.header-top {
    background-color: hsl(var(--black));
    border-bottom: 1px solid hsl(var(--white) / .2);
    padding: 10px 0px;
    position: relative;

    .social-list.style-two .social-list__link flex-center {
        width: 30px;
        height: 30px;
        @extend .fs-12;
    }
}

.top-header-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    @include md-screen {
        justify-content: center;
    }
}

.contact-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    &__item {
        padding-right: 20px;
        margin-right: 20px;
        position: relative;

        &:last-child {
            padding-right: 0;
            margin-right: 0;

            &::before {
                display: none;
            }
        }

        &::before {
            position: absolute;
            content: "";
            width: 1px;
            height: 15px;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: hsl(var(--white) /.4);
        }

        &-icon {
            @extend .fs-12;
            width: 30px;
            height: 30px;
            line-height: 30px;
            text-align: center;
            color: hsl(var(--white));
            background-color: hsl(var(--base));
            border-radius: 50%;
            margin-right: 10px;
            display: inline-block;
        }
    }

    &__link {
        color: hsl(var(--white));
        font-weight: 400;
        font-family: var(--body-font);

        &:hover &-icon {
            color: hsl(var(--white));
        }
    }
}

/* Login Registration */
.login-registration-list {
    margin: 0 -10px;

    @include md-screen {
        padding-right: 0;
    }

    &__item {
        color: hsl(var(--white));
        padding: 0 8px;
        position: relative;

        &:last-child::before {
            display: none;
        }

        &::before {
            position: absolute;
            content: "";
            width: 1px;
            height: 12px;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: hsl(var(--white)/.5);
        }
    }

    &__icon {
        color: hsl(var(--base));
        margin-right: 10px;
        @extend .fs-16;
    }

    &__link {
        color: hsl(var(--white));
        font-weight: 400;
        font-family: var(--body-font);

        @include msm-screen {
            @include fs-15;
        }

        &:hover {
            color: hsl(var(--base));

            .login-registration-list__icon {
                color: hsl(var(--white));
            }

            @include md-screen {
                color: hsl(var(--base));
            }
        }
    }
}

/* Language Box */
.language-box {
    display: flex;
    align-items: center;
    position: relative;

    .select {
        color: hsl(var(--white));
        background-color: transparent;
        border: 1px solid hsl(var(--white)/0.4);
        padding: 5px 8px;
        @extend .fs-15;
        font-weight: 400;
        border-radius: 5px;

        option {
            background-color: hsl(var(--black));
        }
    }
}

/* ================ Top Header End Here ================ */
