
.order-card {
    padding: clampCal(20, 32);
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;
    box-shadow: var(--sm-box-shadow);
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 16px;

    &:not(:last-child) {
        margin-bottom: 16px;
    }

    @include sm-screen {
        flex-direction: column;
    }

    &__image {
        width: 100px;
    }

    &__icon {
        height: 56px;
        width: 56px;
        background-color: hsl(var(--base));
        border-radius: 8px;
        font-size: 32px;
        display: grid;
        place-content: center;
        color: hsl(var(--white));
        flex-shrink: 0;

        @include md-screen {
            height: 40px;
            width: 40px;
            font-size: 24px;
        }
    }

    .content {
        flex: 1;
    }

    &__header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: clampCal(18, 32);

    }

    &__title {
        font-weight: 500;
        margin-bottom: 12px;
        line-height: 1.2;
    }

    .input-group {
        .form--control {
            border-right: 0;
        }
    }

    .form--control {
        padding: 13px 8px;
        color: hsl(var(--light));
        border-color: hsl(var(--border-color));

        &::placeholder {
            font-size: 16px;
            color: hsl(var(--black) / .5);
        }
    }

    .input-group-text {
        background-color: hsl(var(--bg-color));
        border-color: hsl(var(--border-color));
        border-left: 0px;
        cursor: pointer;

        i {
            color: hsl(var(--base));
            transform: scale(1.5);
        }
    }

    .btn--md {
        padding: 10px 24px;
    }
}

.edit-button {
    display: none;
    align-items: center;
    gap: 8px;
    color: hsl(var(--base));

    .icon {
        font-size: 24px;
    }

    .text {
        font-size: 16px;
        font-weight: 600;
    }
}

.order-confirmation {
    .order-card {
        &__title {
            margin-bottom: clampCal(10, 16);
        }

        &__text {
            margin-bottom: clampCal(10, 16);
        }

        &__button {
            gap: 16px;

            .btn--md {
                @extend .fs-16;
            }
        }

        &__list {
            margin-top: clampCal(40, 70);

            .title {
                margin-bottom: clampCal(10, 16);
                font-weight: 500;
            }

            .list-inner {
                list-style: decimal;
                padding-left: 20px;

                .list-item {
                    line-height: 1.5;

                    &:not(:last-child) {
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}

.order-timeline {
    width: 100%;

    &__list {
        .order-timeline-item {
            display: flex;
            justify-content: space-between;

            @include sm-screen {
                flex-direction: column;
            }

            &:not(:last-child) {
                padding-bottom: 40px;

                @include md-screen {
                    padding-bottom: 30px;
                }
                .divide::before {
                    content: "";
                    position: absolute;
                    height: 85px;
                    width: 2px;
                    top: 0px;
                    left: 50%;
                    transform: translateX(-50%);
                    background-color: hsl(var(--base));

                    @include md-screen {
                        height: 60px;
                    }

                    @include sm-screen {
                        height: 85px;
                    }
                }
            }

            .left {
                display: flex;
                align-items: center;
                gap: 40px;

                @include msm-screen {
                    gap: 15px;
                    flex-direction: column;
                    align-items: flex-start;
                }
            }

            .timeline-date {
                color: hsl(var(--black));
                font-weight: 500;
                min-width: 100px
            }

            .divide {
                height: 24px;
                width: 24px;
                flex-shrink: 0;
                border-radius: 0px;
                background: hsl(var(--base));
                border-radius: 50%;
                position: relative;

                @include msm-screen {
                    display: none;
                }


                &::after {
                    content: "";
                    position: absolute;
                    height: 14px;
                    width: 14px;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    outline: 2px solid hsl(var(--white));
                    border-radius: 50%;
                }
            }

            .text,
            .timeline-time {
                font-weight: 500;
            }
        }
    }
}
