.service-catgory-form {
    max-width: 242px;
    width: 100%;

    .form-select {
        font-size: 18px;
        background-position: right 0.8rem center !important;
        background-size: 19px 17px !important;
        font-weight: 500;
    }
}

.service-content {
    padding: clampCal(24, 48);
    background-color: hsl(var(--bg-color));
    border-radius: 16px;
}

.service-view-button {
    margin-top: 40px;
}

.service-content__heading {
    font-weight: 500;
    margin-bottom: clampCal(18, 32);
}

.service-item {
    display: block;

    &:hover {
        .service-item {
            &__title {
                color: hsl(var(--base));
            }

            &__image {
                box-shadow: var(--lg-box-shadow);
            }
        }
    }

    &__image {
        transition: 0.2s linear;

        img {
            width: 100%;
            border-radius: 4px;
        }

        margin-bottom: 16px;
    }

    &__title {
        font-weight: 500;
        margin-bottom: 0px;
        transition: all linear 0.3s;
    }
}
