
.service-provider,
.order-confirmation,
.order-details,
.payment-complete,
.profile,
.placeorder-section,
.blog-detials,
.review,
.service-details{
  @include md-screen{
    margin-top: 60px;
  }
}

/* ====================== Section Heading ==================== */
.section-heading {
  text-align: center;
  margin-bottom: 50px;

  @include lg-screen {
    margin-bottom: 32px;
  }

  @include md-screen {
    margin-bottom: 24px;
  }

  &__title {
    position: relative;
    font-weight: 500;
    margin-bottom: 0px;
  }

  &__desc {
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 24px;
    margin-bottom: 0px;
    font-weight: 400;
  }

  &__subtitle {
    margin-bottom: 16px;
  }

  // Style left
  &.style-left {
    text-align: left;

    .section-heading {
      &__desc {
        margin-left: 0;
      }
    }
  }
}

/* ====================== Section Heading En d==================== */
