/* ================================= Social Icon Css Start =========================== */
.social-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px;

    &__item {
        margin-right: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        font-size: 24px;
        position: relative;
        overflow: hidden;
        transition: .3s;
        cursor: pointer;
        color: hsl(var(--heading-color));

        &.active,
        &:hover,
        &:focus {
            color: hsl(var(--base)) !important;
        }

        @include sm-screen {
            @include fs-20;
        }
    }
}

/* ================================= Social Icon Css End ===========================  */
