.product-card {
    padding: 16px 16px 32px;
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;
    box-shadow: var(--sm-box-shadow);

    &__image {
        img {
            border-radius: 8px;
            width: 100%;
        }
    }

    &__content {
        padding-top: clampCal(16, 24);
    }

    &__heading {
        margin: 8px 0;
        font-weight: 500;
    }

    &__price {
        color: hsl(var(--success));
        @extend .fs-20;
        font-weight: 500;
        margin-bottom: clampCal(24, 48);
    }

    .btn--md {
        border-width: 1px !important;
        padding: 10px 24px;
    }
}

.product-card--row {
    display: flex;
    align-items: center;
    gap: 16px;

    @include msm-screen {
        flex-direction: column;
    }

    .product-card__content {
        padding: 0;
        width: calc(52% - 16px);

        @include msm-screen {
            width: 100%;
        }
    }

    .product-card__image {
        width: 48%;

        @include msm-screen {
            @include msm-screen {
                width: 100%;
            }
        }
    }

    .product-card__text {
        margin: 6px 0;
    }

    .product-card__price {
        margin: 0;
    }

}

.order-complete {
    font-weight: 600;
    color: hsl(var(--black));
    padding: 4px 10px;
    border-radius: 8px;
    box-shadow: var(--sm-box-shadow);
    display: inline-block;
    border: 1px solid hsl(var(--border-color));
    margin-top: 16px;

    .icon {
        color: hsl(var(--success));
    }
}

.order-card {
    &__content {
        width: 100%;
    }

    &__heading {
        font-weight: 500;
        margin-bottom: clampCal(20, 32);
        display: block;
    }
}

.shedule-wrapper {
    display: grid;
    grid-template-columns: repeat(3, auto);
    gap: 12px;

    @include xxsm-screen {
        grid-template-columns: repeat(2, auto);
    }

    .shedule-item {
        .text {
            &:not(:last-child) {
                margin-bottom: 6px;
            }
        }
    }

    .shedule-divide {
        width: 4px;
        height: 100%;
        background-color: hsl(var(--border-color));

        @include xxsm-screen {
            display: none;
        }
    }
}

.shedule-info-item {
    display: flex;
    align-items: flex-start;
    gap: 16px;

    @include xxsm-screen {
        flex-direction: column;
    }

    &:not(:last-child) {
        margin-bottom: clampCal(16, 24);
    }

    .icon {
        height: 40px;
        width: 40px;
        background-color: hsl(var(--base));
        border-radius: 8px;
        font-size: 24px;
        display: grid;
        place-content: center;
        color: hsl(var(--white));
        flex-shrink: 0;
    }

    .content {
        .heading {
            font-weight: 500;
            color: hsl(var(--black));
            margin-bottom: 8px;
        }

        .text {
            max-width: 270px;

        }
    }

}

.provider-profile {
    padding: 16px;
    border: 1px solid hsl(var(--border-color));
    border-radius: 8px;
    box-shadow: var(--sm-box-shadow);

    &__wrapper {
        display: flex;
        align-items: flex-start;
        gap: 16px;

        @include xxsm-screen {
            flex-direction: column;
        }
    }

    &__image {
        flex-shrink: 0;

        img {
            width: 147px;
            height: 107px;
            border-radius: 8px;
            object-fit: cover;

            @include xxsm-screen {
                width: 100%;
                height: auto;
            }
        }
    }

    .content {
        flex: 1;
    }

    &__designation {
        margin-bottom: 8px;
        font-weight: 500;
    }

    &__name {
        color: hsl(var(--black));
        margin-bottom: 8px;
    }

    &__tel {
        gap: 8px;

        .icon {
            color: hsl(var(--base));
            font-size: 24px;
        }
    }

    &__title {
        margin-top: clampCal(20, 32);
        margin-bottom: clampCal(16, 24);
        font-weight: 500;
    }
}

.comment {
    padding: 24px 24px 40px;
    box-shadow: var(--sm-box-shadow);
    border-radius: 8px;
    border: 1px solid hsl(var(--border-color));

    &__title {
        font-weight: 500;
        margin-bottom: clampCal(16, 24);
    }
}

.comment-wrapper {
    &__title {
        margin-top: clampCal(16, 24);
        margin-bottom: 8px;
        color: hsl(var(--black));
        font-weight: 500;
    }

    &__item {
        padding: 12px 16px;
        border: 1px solid hsl(var(--border-color));
        color: hsl(var(--light));
        line-height: 1.5;
        border-radius: 8px;
    }

    &__text {
        max-width: 380px;
        width: 100%;
    }
}
